import { Button, Form, Input, Modal, Select } from "antd";
import { DatePicker } from "antd-jalali";
import { useForm } from "antd/lib/form/Form";
import Logo from "../../Components/Logo/Logo";
import axios from "axios";
import { useEffect, useState } from "react";

const NewKid = () => {
  const [loading, setLoading] = useState(false);
  const URL = "https://mesbahkids.ir/api2";

  const addNewKid = (values) => {
    setLoading(true);
    const birth_date = new Date(values.birth_date).toISOString().substring(0, 10)
    console.log(birth_date)
    axios
      .post(URL + "/kids" , {...values , birth_date})
      .then((res) => {
        Modal.success({ title: "     ." });
        setLoading(false);
      })
      .catch((err) => {
        Modal.error({ title: "        ." });
        setLoading(false);
      });
  };

  const [form] = useForm();
  return (
    <div className="form">
      <Logo />
      <h2>      </h2>
      <Form 
      onFinish={(values)=>{
        addNewKid(values)
      }}
      layout="vertical" 
      form={form} style={{ width: "100%" }}>
        <Form.Item
          label=" "
          name="first_name"
          rules={[
            {
              required: true,
              message: "     .",
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          label="  "
          name="last_name"
          rules={[
            {
              required: true,
              message: "     .",
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          label="  "
          name="birth_date"
          rules={[
            {
              required: true,
              message: "     .",
            },
          ]}
        >
          <DatePicker style={{ width: "100%" }} />
        </Form.Item>
        <Form.Item
          label=" "
          name="gender"
          rules={[
            {
              required: true,
              message: "     .",
            },
          ]}
        >
          <Select placeholder=" ">
            <Select.Option value="FE"> </Select.Option>
            <Select.Option value="MA"></Select.Option>
          </Select>
        </Form.Item>
        <Form.Item
          label=" "
          name="caretaker_name"
          rules={[
            {
              required: true,
              message: "     .",
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          label="    "
          name="caretaker"
          rules={[
            {
              required: true,
              message: "     .",
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          label="  "
          name="caretaker_phone_number"
          rules={[
            {
              required: true,
              message: "     .",
            },
          ]}
        >
          <Input type="number" />
        </Form.Item>
        <Form.Item
          label="  "
          name="emergancy_calls"
          rules={[
            {
              required: true,
              message: "     .",
            },
          ]}
        >
          <Input type="number" />
        </Form.Item>
        <Form.Item
          label="        "
          name="wc"
          rules={[
            {
              required: true,
              message: "     .",
            },
          ]}
        >
          <Select placeholder=" ">
            <Select.Option value={true}> </Select.Option>
            <Select.Option value={false}></Select.Option>
          </Select>
        </Form.Item>
        <Form.Item>
          <Button
            loading={loading}
            style={{ width: "100%" }}
            type="primary"
            htmlType="submit"
          >
             
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
};

export default NewKid;
